import React, { useState } from "react";
import axios from "axios";
import {
	Form,
	Input,
	InputNumber,
	Row,
	Col,
	DatePicker,
	message,
	Skeleton,
} from "antd";
import styled from "styled-components";
import { customMedia } from "../../GlobalStyles";

import Button from "../../Button";
import Tag from "../../Tag";
import MapContainer from "../../MapContainer";

const RegisterForm = ({ ...props }) => {
	const [registerForm] = Form.useForm();
	const [inputText, setInputText] = useState("");
	const [addressStreet, setAddressStreet] = useState("");
	const [addressDetail, setAddressDetail] = useState("");
	const [imgFile, setImgFile] = useState(null);
	const [preview, setPreview] = useState(null);
	const [selectedTags, setSelectedTags] = useState([]);
	const tags = [
		"",
		"",
		"",
		"/",
		"",
		"",
		"",
		"  ",
	];

	const fullAddress = addressStreet + addressDetail;
	const userId = localStorage.getItem("user_id");

	const onChange = (e) => {
		setInputText(e.target.value);
	};

	const getaddressStreet = () => {
		setAddressStreet(inputText);
		setInputText("");
	};

	const getaddressDetail = () => {
		setAddressDetail(inputText);
		setInputText("");
	};

	const getFullAdress = (e) => {
		getaddressStreet();
		getaddressDetail();
	};

	const handleImgChange = (e) => {
		let file = e.target.files[0];
		let reader = new FileReader();

		reader.onloadend = () => {
			setImgFile(file);
			setPreview(reader.result);
		};
		if (file) {
			reader.readAsDataURL(file);
		}
	};

	const handleSelectTags = (e) => {
		let tagName = e.target.innerText;
		let index = selectedTags.indexOf(tagName);

		if (selectedTags.includes(tagName)) {
			selectedTags.splice(index, 1);
			setSelectedTags([...selectedTags]);
		} else if (selectedTags.length === 3) {
			selectedTags.splice(index, 1);
			message.error("  3  !");
		} else {
			setSelectedTags([...selectedTags, tagName]);
		}
	};

	const sendData = async (values) => {
		const startDate = values.date[0]._d.toISOString().substring(0, 10);
		const endDate = values.date[1]._d.toISOString().substring(0, 10);
		const sendTags = selectedTags.join(", ");
		const formData = new FormData();

		if (!values.minPersonnel || !values.maxPersonnel) {
			message.error(" .");
			return;
		}

		if (!sendTags) {
			message.error(" .");
			return;
		}

		formData.append("userId", userId);
		formData.append("title", values.title);
		formData.append("contents", values.contents);
		formData.append("startDate", startDate);
		formData.append("endDate", endDate);
		formData.append("minPersonnel", values.minPersonnel);
		formData.append("maxPersonnel", values.maxPersonnel);
		formData.append("img", imgFile);
		formData.append("tags", sendTags);
		formData.append("bookTitle", values.bookTitle);
		formData.append("author", values.author);
		formData.append("publisher", values.publisher);
		formData.append("publishedAt", values.publishedAt);
		formData.append("bookDescription", values.bookDescription);
		formData.append("description", values.description);
		formData.append("addressStreet", values.addressStreet);
		formData.append("addressDetail", values.addressDetail);

		try {
			const res = await axios.get(`/clubs/users/${userId}`);

			if (res.status === 204) {
				const res = await axios.post("/clubs", formData);

				if (res.status === 200) {
					registerForm.resetFields();
					setImgFile();
					message.success("  !");
					props.onCancel();
				} else {
					message.error("  .");
				}
			} else if (res.data) {
				registerForm.resetFields();
				setImgFile();
				message.error("   .");
			}
		} catch (err) {
			console.log(err);
		}
	};

	const onFinish = async (values) => {
		console.log("form values: ", values);
		sendData(values);
	};

	const onFinishFailed = (errorInfo) => {
		console.log("Failed: ", errorInfo);
	};

	return (
		<Wrapper>
			<StyledForm
				form={registerForm}
				name="registerForm"
				layout="vertical"
				onFinish={onFinish}
				onFinishFailed={onFinishFailed}
			>
				<Row gutter={32}>
					<Col span={16}>
						<Form.Item
							label=""
							name="title"
							rules={[{ required: true, message: "  ." }]}
						>
							<StyledInput placeholder="" />
						</Form.Item>
						<Form.Item
							label="  "
							name="contents"
							rules={[
								{ required: true, message: "    ." },
							]}
						>
							<StyledInput placeholder="  " />
						</Form.Item>
						<Form.Item
							label=" "
							rules={[
								{
									required: true,
									message: "   .",
								},
							]}
						>
							<Row>
								<PersonnelRow>
									<Form.Item name="minPersonnel">
										<StyledInputNumber min={2} placeholder={2} />
									</Form.Item>
									<StyledSpan>  ~ </StyledSpan>
								</PersonnelRow>
								<PersonnelRow>
									<Form.Item name="maxPersonnel">
										<StyledInputNumber min={2} placeholder={2} />
									</Form.Item>
									<StyledSpan>  </StyledSpan>
								</PersonnelRow>
							</Row>
						</Form.Item>
						<Form.Item
							label=" "
							name="date"
							rules={[
								{
									type: "array",
									required: "true",
									message: "   .",
								},
							]}
						>
							<StyledRangePicker />
						</Form.Item>
					</Col>
					<Col span={8}>
						<Form.Item
							label=""
							name="img"
							rules={[
								{ required: false, message: "  ." },
							]}
							style={{ textAlign: "center" }}
						>
							<Row gutter={[0, 24]} justify="center">
								{!preview ? (
									<SkeletonImg />
								) : (
									<PreviewImage
										src={preview}
										alt="Preview image"
									></PreviewImage>
								)}
								<FileInput>
									<input
										type="file"
										accept="image/*"
										onChange={handleImgChange}
									/>
								</FileInput>
							</Row>
						</Form.Item>
					</Col>
				</Row>
				<TagRow>
					<TagTitle> (3  )</TagTitle>
					<TagContainer>
						{tags.map((tag, i) => (
							<Tag
								type="button"
								key={i}
								value={i}
								onClick={handleSelectTags}
								selected={selectedTags.includes(tag) ? true : false}
							>
								{tag}
							</Tag>
						))}
					</TagContainer>
				</TagRow>
				<TitleRow></TitleRow>
				<Col span={16}>
					<Form.Item
						label=""
						name="bookTitle"
						rules={[{ required: true, message: " ." }]}
					>
						<StyledInput placeholder="" />
					</Form.Item>
				</Col>
				<Col span={16}>
					<Form.Item
						label=""
						name="author"
						rules={[{ required: true, message: " ." }]}
					>
						<StyledInput placeholder="" />
					</Form.Item>
				</Col>
				<Col span={16}>
					<Form.Item label="" name="publisher">
						<StyledInput placeholder="" />
					</Form.Item>
				</Col>
				<Col span={16}>
					<Form.Item initialValue={0} label="" name="publishedAt">
						<StyledInputNumber placeholder={1900} />
					</Form.Item>
				</Col>
				<Col span={16}>
					<Form.Item
						label="    "
						name="bookDescription"
						rules={[
							{
								required: true,
								message: "     .",
							},
						]}
					>
						<StyledTextArea
							rows={10}
							placeholder={"     ."}
						/>
					</Form.Item>
				</Col>
				<Row>
					<Col span={16}>
						<Form.Item
							label=""
							name="description"
							rules={[
								{ required: true, message: "  ." },
							]}
						>
							<StyledTextArea
								rows={10}
								placeholder={
									"  ,     ."
								}
							/>
						</Form.Item>
					</Col>
				</Row>
				<Row>
					<Col span={16}>
						<Form.Item label="">
							<Form.Item name="addressStreet">
								<StyledInput
									placeholder=" "
									onChange={onChange}
									value={inputText}
								/>
							</Form.Item>
							<Form.Item name="addressDetail">
								<StyledInput placeholder=" " />
							</Form.Item>
						</Form.Item>
						<FilledBtn type="button" onClick={getFullAdress}>
							 
						</FilledBtn>
						<MapWrapper>
							<MapContainer searchSpot={fullAddress} />
						</MapWrapper>
					</Col>
				</Row>
				<ButtonRow>
					<FilledBtn></FilledBtn>
					<UnfilledBtn type="button" onClick={props.onCancel}>
						
					</UnfilledBtn>
				</ButtonRow>
			</StyledForm>
		</Wrapper>
	);
};

export default RegisterForm;

